.transactionHistory {
  width: 600px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
}
.headtitle {
  background-color: #00b7ffb1;
}
.headTitleText {
  text-align: center;
  padding-top: 6px;
  padding-bottom: 6px;
  width: 200px;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.11;
  letter-spacing: 0.02em;
  color: #eef5ef;
}
.bodyCellText {
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 60px;
  width: 200px;
  font-weight: 600;
  font-size: 22px;
  line-height: 1.17;
  letter-spacing: 0.02em;
  color: #838383;
}
.bodyRowText:nth-child(even) {
  background-color: #e5f1e7;
}
.bodyRowText:nth-child(odd) {
  background-color: #c8cfc9;
}
